import { Directive, ElementRef, HostListener, NgModule, Input } from '@angular/core';

@Directive({
  selector: '[mat-button]'
})

@NgModule({
    imports: [
    ],
    exports: [
        HighlightDirective
  ],
  })
export class HighlightDirective {

    constructor(private el: ElementRef) {
        const hostElem = this.el.nativeElement;
        if(el.nativeElement.getAttribute("movil-button") == "false"){ 
        el.nativeElement.style.width = '16%';
        el.nativeElement.style.fontSize = '17px';
        el.nativeElement.style.color = 'White';
        el.nativeElement.style.fontFamily = 'Arial';
        }else{

        }
    }

  @HostListener('mouseenter') onMouseEnter() {
    if(this.el.nativeElement.getAttribute("movil-button") == "false"){ 
    this.highlight('Blue');
    }
  }
 
  @HostListener('mouseleave') onMouseLeave() {
    if(this.el.nativeElement.getAttribute("movil-button") == "false"){ 
    this.highlight('White');
    }
  }
 
  private highlight(color: string) {
    if(this.el.nativeElement.getAttribute("movil-button") == "false"){ 
    this.el.nativeElement.style.color = color;
    }
  }

}